.count-timer-display {
    display: flex;
    justify-content: space-between;
    width: 400px;
    
    .clock-column {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        position: relative;

        &:after {
            content: ':';
            display: block;
            position: absolute;
            right: -40px;
            font-size: 30px;
        }

        &:last-child::after {
            display: none;
        }
    }
}